.contact {
    overflow: hidden;
}


/* Form */

input,
textarea {
    width: 100%;
    padding: 8px;
    margin-bottom: 5px;
    border: none;
    background: rgba(0, 0, 0, .1);
    border-radius: 5px;
    color: var(--black);
    font-family: inherit;
    font-size: inherit;
    -webkit-appearance: none;
    transition: all .3s;
}

textarea {
    resize: none;
    margin-top: 5px;
}

label {
    margin-bottom: 5px;
}

.contact-heading {
    margin-bottom: 20px;
}

.form-block {
    gap: 10px;
    margin-bottom: 5px;
}

@media (max-width: 768px) {
    .form-block {
        flex-direction: column;
    }
}

.btn {
    background: var(--red);
    color: var(--light) !important;
    border-radius: 5px;
    color: var(--black);
    line-height: 14px;
    width: 100px;
    height: 40px;
}

.btn:focus,
.btn:active {
    outline: none !important;
    box-shadow: none;
}


/* sending animation */

.btn-text,
.success-circle,
.failure-circle {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.success-circle,
.failure-circle {
    opacity: 0;
}


/* Success and faliuer Animation */

.success-circle,
.failure-circle {
    width: 20px;
    height: 20px;
    border-radius: 50%;
}

.failure-circle,
.success-circle {
    background: #fff;
}

.success-symbol,
.failure-symbol {
    opacity: 0;
}