/* About */

.about-content {
    margin-top: 20px;
}

.header {
    position: absolute;
    top: 0;
}

.about-content {
    text-align: center;
}

.lower {
    min-height: 120px;
}

.upper-heading {
    display: block;
    transform: translateY(150px);
}

.lower-text {
    font-size: 21px;
    font-weight: 200;
    padding-top: 10px;
    max-width: 500px;
    width: 100%;
    transform: translateY(-150px);
}

@media (max-width: 768px) {
    .lower-text {
        font-size: 18px;
    }
}

.line {
    width: 80%;
    height: 5px;
    border-radius: 5px;
    background: var(--red);
    transform: scaleX(0);
}


/* animation starter */

.contact-links,
.logo {
    transform: translateY(-100px);
}

.logo {
    font-size: 29px;
    font-weight: 700;
    line-height: 29px;
    cursor: default;
    transition: all .3s;
    user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
}

.dot {
    display: inline-block;
    color: var(--red);
}


/* Contact Links */

.contact-links {
    transition: all .3s;
}

.contact-links a {
    line-height: 10px;
}

.contact-links a:first-child {
    margin-right: 20px;
}

.contact-links svg {
    fill: var(--black);
    transition: all .3s;
}

.contact-links svg:hover {
    fill: var(--red);
}