:root {
    --black: #2c2c2c;
    --green: #33BE91;
    --red: #e42525;
    --gray: #424242;
    --light: #F7F7F7;
    --shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    outline: none !important;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
    font-family: 'Poppins', sans-serif;
    color: var(--black);
    background-color: #f7f7f7;
    background-image: url("data:image/svg+xml,%3Csvg width='40' height='40' viewBox='0 0 40 40' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23ececec' fill-opacity='0.4' fill-rule='evenodd'%3E%3Cpath d='M0 40L40 0H20L0 20M40 40V20L20 40'/%3E%3C/g%3E%3C/svg%3E");
}

a,
a:link,
a:visited {
    text-decoration: none;
    cursor: pointer;
    color: var(--black);
    transition: all .5s;
}

a:hover {
    color: var(--red);
}

h1 {
    font-size: 50px;
    line-height: 60px;
    font-weight: 700;
}

@media (max-width: 768px) {
    h1 {
        font-size: 35px;
        line-height: 45px;
    }
}

img {
    max-width: 100%;
    height: auto;
}

button {
    cursor: pointer;
    font: inherit;
    border: none;
    line-height: 14px;
}

.container {
    max-width: 900px;
    width: 100%;
    margin: 0 auto;
}

.spacer {
    flex-grow: 1;
}

.text-center {
    text-align: center;
}


/* Components */

.about,
.work,
.contact {
    min-height: 100vh;
}


/* Scroll bar */


/* width */

main::-webkit-scrollbar {
    width: 10px;
}


/* Track */

main::-webkit-scrollbar-track {
    background: var(--gray);
    border-radius: 10px;
}


/* Handle */

main::-webkit-scrollbar-thumb {
    background: var(--black);
    border-radius: 10px;
}


/* Handle on hover */

main::-webkit-scrollbar-thumb:hover {
    background: var(--black);
}


/* Spacing and positioning */

.p-20 {
    padding: 20px;
}


/* Shared */

.orientation,
.loader {
    width: 100%;
    height: 100vh;
}

.orientation svg {
    fill: var(--light-gray);
    margin-bottom: 1rem;
}

.loader {
    color: var(--light);
    position: relative;
}

.loader-slide {
    width: 100%;
    height: 25%;
    z-index: 1;
    background: var(--red);
}

.loader-content {
    position: absolute;
    top: 0;
    left: 0;
}

.loader h1 {
    transform: translateY(100px);
}