.work {
    padding: 20px;
    overflow: hidden;
}

.project {
    cursor: pointer;
    position: relative;
    display: block;
    max-width: 25rem;
    overflow: hidden;
    box-shadow: var(--shadow);
}

.project:hover .project-content {
    clip-path: circle(75%);
}

.project-content {
    position: absolute;
    top: 0;
    left: 0;
    transition: all 0.2s ease-in-out;
    background: var(--red);
    clip-path: circle(10% at 100% 0%);
    transition: clip-path .5s ease-in-out;
    padding: 20px;
    color: var(--light);
}


/* project typography */

.project-title {
    font-size: 18px !important;
    line-height: 18px;
    font-weight: 700;
}

.project p {
    margin: 5px 0;
    font-size: 13px;
}

.project-details {
    flex-grow: 1;
}


/* Project Tags */

.project-tags {
    font-size: 11px;
}

.tag-circle {
    display: inline-block;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    border: 2px solid #fff;
    margin-right: 3px;
    margin-bottom: 1px;
}


/* project tags */

.tag {
    color: var(--light);
}

.tag:not(:last-child) {
    margin-right: 5px;
}


/* project links */

.project a {
    font-size: 11px;
    background: var(--light);
    color: var(--red);
    padding: 4px 6px;
    border-radius: 5px;
    transition: all .5s;
}

.project-footer a:first-child {
    margin-right: 20px;
}

.project-footer a svg {
    margin-right: 5px;
}

.project-footer a:hover {
    transform: translateY(-5px);
}